import React from "react"
import Layout from "../components/layout"
import Banner from "../components/banner"
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import { Link } from 'gatsby'
import SEO from "../components/seo"
import "../styles/table.css"
import {useTranslation} from 'react-i18next';
import OrgIcon from '../../assets/menu_icon_agencies.svg';

const { SearchBar } = Search;

export default ({ pageContext: { organizationTable } }) => {

  const { t, i18n } = useTranslation()

  return (
      <Layout>
        <SEO title={ t('organizations.title') } lang={ i18n.language } />
        <Banner title={ t('organizations.title') }
                subtitle={ t('organizations.subtitle') }
                image='https://images.bible.cloud/fab/banners/agencies.jpg'
                icon={ OrgIcon }
                subnav={[
                  {path: '/members/', title: t('organizations.fobai.title')},
                  {path: '/organizations/', title: t('organizations.title')}
                ]}
                breadcrumbs={[
                  {path: '/', title: 'home'},
                  {path: '/organizations/', title: t('organizations.title')}
                ]}
        />
        <div className="row">
          <div className="medium-8 columns centered thead-hide">
            <ToolkitProvider
                keyField="id"
                data={ organizationTable }
                bordered={ false }
                columns={[
                  {
                    dataField: 'id',
                    text: 'id',
                    hidden: true,
                    keyField: true
                  },
                  {
                    dataField: 'name',
                    text: 'English Name',
                    sort: true,
                    formatter: (cell, row) => <Link to={`/organizations/${row.slug}`}>{ cell }</Link>
                  }
                ]}
                search
            >
              {
                props => (
                    <div>
                      <SearchBar { ...props.searchProps } delay={ 100 } placeholder={t('search.title') + " " + organizationTable.length + " " + t('organizations.title')} />
                      <BootstrapTable
                          pagination={ paginationFactory() }
                          { ...props.baseProps }
                      />
                    </div>
                )
              }
            </ToolkitProvider>
          </div>
        </div>

      </Layout>
  )
}